html, body {
	margin: 0;
	padding: 0;
	height: 100vh;
	background-image: linear-gradient(45deg, #6561ed, #26c3de);
	font-family: 'Source Sans Pro', serif;
	color: white;
	overflow-x: hidden
}

.container {
	text-align: center;
	margin-top: 20vh;
	font-weight: 200;
}

.title {
	color: white;
	font-size: 4.5rem;
	font-weight: 200;
	margin-bottom: 50px;
}

.description {
	color: white;
	font-size: 2.25rem;
	font-weight: 200;
}

.main-button {
    background: transparent;
    padding: 20px 40px 20px;
    color: white;
    border-radius: 20px;
    cursor: pointer;
    border: 1px solid;
    transition: background 200ms, color 200ms, border 200ms;
    font-size: 2rem;
    letter-spacing: 1.5px;
    outline: none;
    box-shadow: 0 3px 15px -7px black;
    margin: 25px;
    font-weight: 200;
}

.main-button:hover {
    background: black;
    color: white;
    border: 1px solid transparent;
}

@media screen and (max-width: 767px) {
	.container {
		margin-top: 5vh;
	}
	.login-button {
		font-size: 1rem;
	}
	.title {
		font-size: 3.5rem;
	}
	.description {
		font-size: 1.75rem;
		padding: 0px 30px;
	}
}
.title-container {
	margin-bottom: 50px;
}

.login.title {
	-webkit-animation-delay: 0ms;
	        animation-delay: 0ms;
}

.login.description {
	-webkit-animation-delay: 150ms;
	        animation-delay: 150ms;
}

.login-button {
	-webkit-animation-delay: 300ms;
	        animation-delay: 300ms;
}

.login.description, .login.title, .login-button {
	-webkit-animation-duration: 750ms;
	        animation-duration: 750ms;
}

@media screen and (max-width: 767px) {
	.login-button {
		font-size: 18px;
	}
}
.description.margin {
    margin-bottom: 15px;
}

.saved-user.title {
    -webkit-animation-delay: 0ms;
            animation-delay: 0ms;
}

.saved-user.description {
    -webkit-animation-delay: 150ms;
            animation-delay: 150ms;
}

.dashboard-button {
    -webkit-animation-delay: 300ms;
            animation-delay: 300ms;
}

.saved-user.description, .saved-user.title, .dashboard-button {
    -webkit-animation-duration: 750ms;
            animation-duration: 750ms;
}

@media screen and (max-width: 767px) {
    .saved-user.description {
        margin-bottom: 30px;
    }
    .dashboard-button {
        font-size: 18px;
    }
}
.spinner-container {
    margin-top: 20vh;
}

.spinner {
    margin: 0 auto;
}

@media screen and (max-width: 767px) {
    .spinner {
        height: 150px !important;
        width: 150px !important;
    }
}
.new-user-hello {
    font-size: 4.5rem;
    margin-bottom: 45px;
    -webkit-animation-delay: 0ms;
            animation-delay: 0ms;
}

.no-back-up {
    font-size: 2.25rem;
    margin-bottom: 10px;
}

.get-started {
    font-size: 2.25rem;
    margin-bottom: 45px;
    -webkit-animation-duration: 750ms;
            animation-duration: 750ms;
}

.new-user-hello, .get-started, .no-back-up, .backup-button {
    -webkit-animation-duration: 750ms;
            animation-duration: 750ms;
}

.get-started, .no-back-up {
    -webkit-animation-delay: 150ms;
            animation-delay: 150ms;
}

.backup-button {
    -webkit-animation-delay: 300ms;
            animation-delay: 300ms;
}

@media screen and (max-width: 767px) {
    .new-user-hello {
        line-height: 1;
        padding: 0px 15px;
        font-size: 4rem;
    }
    .no-back-up, .get-started {
        font-size: 1.75rem;
        margin-bottom: 30px;
        padding: 0px 15px;
        line-height: 1;
    }
    .hello {
        font-size: 2.75rem;
    }
    .backup-button {
        font-size: 18px;
    }
}
.header {
    position: relative;
    top: 0;
    height: auto;
    padding-top: 10px;
    padding-bottom: 10px;
}

.header * {
    -webkit-animation-duration: 750ms;
            animation-duration: 750ms;
}

.sync {
    cursor: pointer;
    vertical-align: bottom;
    transition: -webkit-transform 100ms;
    transition: transform 100ms;
    transition: transform 100ms, -webkit-transform 100ms;
}

.sync:hover {
    -webkit-transform: scale(1.15) !important;
            transform: scale(1.15) !important;
}

.dash-backup-button {
    padding: 15px 25px 15px;
    border-radius: 8px;
    cursor: pointer;
    border: 1px solid white;
    transition: background 200ms, color 200ms, border 200ms;
    font-size: 1.25rem;
    letter-spacing: 1.5px;
    outline: none;
    box-shadow: 0 3px 15px -7px black;
    font-weight: 200;
    margin: 0 20px;
}

.dash-backup-button:hover {
    background: black;
    color: white;
    border: 1px solid transparent;
}

.delete-button {
    padding: 15px 25px 15px;
    border-radius: 8px;
    cursor: pointer;
    border: 1px solid white;
    transition: background 200ms, color 200ms, border 200ms;
    font-size: 1.25rem;
    letter-spacing: 1.5px;
    outline: none;
    box-shadow: 0 3px 15px -7px black;
    font-weight: 200;
    margin: 0 20px;
    float: right;
    color: white;
    background: transparent;
}

@media screen and (max-width: 767px) {
    .dash-backup-button, .delete-button {
        font-size: 18px;
        padding: 7px 15px 7px;
        margin: 0 10px;
    }
    .dash-backup-button {
        float: left;
    }
    .sync {
        width: 40px;
    }
}
.card {
    width: 250px;
    height: 370px;
    background: black;
    border-radius: 20px;
    border: 2px solid white;
    margin: 25px;
    box-shadow: 0px 0px 100px -25px black;
    transition: -webkit-transform 100ms;
    transition: transform 100ms;
    transition: transform 100ms, -webkit-transform 100ms;
    display: inline-block;
}

.card-image {
    width: 250px;
    height: 250px;
}

.card-image img {
    width: 100%;
    height: 100%;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
}

.card-song-title {
    margin: 20px auto;
    font-size: 1.5rem;
    text-align: center;
    line-height: 1;
    font-weight: 200;
}

.card-song-artist {
    margin: 10px auto;
    font-size: 1.2rem;
    text-align: center;
    font-style: italic;
    font-weight: 200;
}

.card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

@media screen and (max-width: 767px) {
    .card:hover {
        -webkit-transform: none;
                transform: none;
    }
    .card {
        overflow-y: scroll;
        margin: 10px;
        height: 350px;
    }
    .card-song-artist, .card-song-title {
        font-size: 25px;
    }
    .card-song-title {
        margin-bottom: 0;
    }
    .card-song-artist {
        margin-top: 5px;
    }
}
.deleted-container {
    margin-top: 25px;
}

.deleted-container .title {
    font-size: 2.75rem;
    display: inline-block;
    border-bottom: 1px solid white;
    padding: 10px 30px;
    -webkit-animation-duration: 750ms;
            animation-duration: 750ms;
    -webkit-animation-delay: 150ms;
            animation-delay: 150ms;
}

ul {
    padding-left: 0;
}

ul li {
    display: inline-block;
}

.deleted-container {
    text-align: center;
    display: block;
    clear: both;
    padding-bottom: 200px;
}

.cards-container {
    -webkit-animation-delay: 300ms;
            animation-delay: 300ms;
    -webkit-animation-duration: 750ms;
            animation-duration: 750ms;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-thumb {
    background: #4d4d4d;
    border-radius: 20px;
    min-height: 100px;
}

::-webkit-scrollbar-track {
    background: linear-gradient(to bottom, #000000, #14238c);
    border-radius: 0px;
}

@-webkit-keyframes loading {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes loading {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@media screen and (max-width: 767px) {
    .deleted-container {
        margin-top: 0;
    }
    .deleted-container .title {
        font-size: 2rem;
        border-bottom: none;
    }
    * {
        -webkit-overflow-scrolling: touch;
    }
}
footer {
	position: absolute;
	bottom: 0;
	background-color: white;
	height: 60px;
	width: 100%;
	border-top: 1px solid black;
	overflow: hidden;
}

.media-list {
	position: relative;
	margin: 0;
	line-height: 60px;
	background: white;
	text-align: center;
}

.media-list li {
	margin: 0 40px;
	font-size: 1.25em;
	font-weight: 300;
}

.media-list a {
	text-decoration: none;
	color: black;
}

.media-list a:hover {
	text-decoration: underline;
	color: #6561ed;
}

@media screen and (max-width: 767px) {
	footer {
		position: fixed;
		height: 50px;
	}
	.media-list {
		line-height: 50px;
	}
	.media-list li {
		margin: 0 15px;
		font-size: 1rem;
	}
}
